.wp-block-create-block-tmy {
	font-size: 64px;
}

.events-wrapper {
	display: grid;
	grid-gap: 10px;
	grid-template-columns: 1fr 1fr 1fr;
	img:not(.events-icon) {
		width: 100%;
		object-fit: cover;
		height: 100%;
	}
}
figure {
	margin: 0;
}
.events-1 {
	grid-column: 1 / 2;
	grid-row: 1;
}
.events-2 {
	grid-column: 2 / 4;
	grid-row: 1 / 3;
}
.events-3 {
	grid-column: 1;
	grid-row: 2;
}
.events-4 {
	grid-column: 3;
	grid-row: 3;
}
